import { Component, OnInit, OnDestroy } from '@angular/core';
import { AppComponentProvider } from './app.component.privider';
import { Subscription } from 'rxjs/Subscription';
@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css'],
  providers: [AppComponentProvider]
})
export class AppComponent {
  title = 'app';
  list: any = [];
  param:any = {
    size: 20,
    id:'',
    type:'1kkk'
  };
  constructor(private service: AppComponentProvider) { }

  ngOnInit(){
    this.getHttp();
  }

  nextPage(){
    this.getHttp();
  }
  typeChange(data){
    console.log(data);
    console.log(this.param.type);

    this.param.id = '';
    this.getHttp();
  }

  ajax: Subscription;
  getHttp() {
    this.ajax = this.service.get(this.param).subscribe((res: any) => {
      console.log(res);

      res = res.json();

      console.log(res);

      if (res.success) {

        this.list = res.data;

        if(this.list.length>0){
          this.param.id = this.list[this.list.length-1]['_id'];
        }
         
      } else {
        this.list = [];
      }
    });
  }

  ngOnDestroy() {
    if (this.ajax) {
      this.ajax.unsubscribe();
    }
  }

}



